.map-icon {
    background-color: #fff;
    padding: 5px;
    font-size: 14px;
    border: 1px solid #000;
    border-radius: 5px;
    width: 40px;
    text-align: center; 
    font-weight: bold;
}
.map-icon.sel {
    background-color: yellowgreen;
    width: 50px;
}


